<template>
<div>
  <el-button type="primary"  @click="upFile()" class="comond_btn">上传</el-button>
  <!-- <product-up :formDate="formDate"></product-up> -->
  <el-table
    :data="tableData"
    v-loading="listLoading"
    style="width: 100%">
 <el-table-column type="expand">
      <template slot-scope="props">
        <el-form label-position="left" inline class="demo-table-expand">
          <el-form-item label="商品名称">
            <span>{{ props.row.name }}</span>
          </el-form-item>
          <el-form-item label="所属分类">
            <span>{{ props.row.typeName }}</span>
          </el-form-item>
          <el-form-item label="品牌名称">
            <span>{{ props.row.barndName }}</span>
          </el-form-item>
          <el-form-item label="产品编码">
            <span>{{ props.row.userDefinedCode }}</span>
          </el-form-item>
          <el-form-item label="创建时间">
            <span>{{ new Date(props.row.createDate) }}</span>
          </el-form-item>
          <el-form-item label="更新时间">
            <span>{{ new Date(props.row.updateDate) }}</span>
          </el-form-item>
        </el-form>
      </template>
    </el-table-column>

    <el-table-column
      label="商品图片"
      width="">
      <template slot-scope="scope">
          <img :src="scope.row.proudctImgUrl" alt="" style="width:40px;height:40px;">
      </template>
    </el-table-column>
    <el-table-column
      label="商品名称"
      width="">
      <template slot-scope="scope">
        <!-- <i class="el-icon-time"></i> -->
        <el-popover trigger="hover" placement="top">
          <p>商品名称: {{ scope.row.name }}</p>
          <div slot="reference" class="name-wrapper">
            <el-tag size="medium">{{ scope.row.name }}</el-tag>
          </div>
        </el-popover>
      </template>
    </el-table-column>
    <el-table-column
      label="销售价格"
      width="">
      <template slot-scope="scope">
        <span style="margin-left: 10px">{{ scope.row.salePrice }}</span>
      </template>
    </el-table-column>
    <el-table-column
      label="欧元价格"
      width="">
      <template slot-scope="scope">
        <span style="margin-left: 10px">{{ scope.row.marketPrice }}</span>
      </template>
    </el-table-column>
    <el-table-column
      label="更新人"
      width="">
      <template slot-scope="scope">
        <span style="margin-left: 10px">{{ scope.row.updateBy }}</span>
      </template>
    </el-table-column>
    <el-table-column label="操作">
      <template slot-scope="scope">
        <el-button
          size="mini"
          @click="handleEdit(scope.$index, scope.row)">编辑</el-button>
        <el-button
          size="mini"
          type="danger"
          @click="handleDelete(scope.$index, scope.row)">删除</el-button>
      </template>
    </el-table-column>
  </el-table>
  <div style="text-align:center;">
      <el-pagination
      background
      layout="prev, pager, next"
      @current-change="handelCurrentChange"
      :current-page="currentPage"
      :total="total">
    </el-pagination>
  </div>
  </div>
</template>
<script>
// import productUp from '../../components/productUp.vue'
import { queryProduct } from '../../api'
export default {
  name: 'table1',
  data () {
    return {
      tableData: [],
      currentPage: 1, //  默认第一页
      total: 0, //  总数
      listLoading: false
    }
  },
  components: {
    // productUp
  },
  mounted () {
    this.getQueryProduct()
  },
  methods: {
    handleEdit (index, row) {
      console.log(index, row)
      // this.$router.push({ name: 'table3', params: row })
      this.$router.push({ path: '/commodityDetail', query: row })
    },
    handleDelete (index, row) {
      console.log(index, row)
    },
    upFile () {
      this.$router.push('commodityDetail')
    },
    handelCurrentChange (i) {
      this.currentPage = i
      this.getQueryProduct()
    },
    getQueryProduct () { //  获取商品列表
      let para = {
        'pageIndex': this.currentPage,
        'isValid': true
      }
      this.listLoading = true
      queryProduct(para).then((res) => {
        console.log(res)
        if (res.data.data === null) {
          this.listLoading = false
          this.$message.error('暂无数据')
        } else {
          this.tableData = res.data.data.productAndShopDtos
          this.total = res.data.data.total
          this.listLoading = false
        }
      })
    }
  }
}
</script>
<style lang="scss" scoped>
.comond_btn {
  float: right;
  margin-right: 30px;
  margin-top: 20px;
}
.demo-table-expand {
    font-size: 0;
  }
  .demo-table-expand label {
    width: 90px;
    color: #99a9bf;
  }
  .demo-table-expand .el-form-item {
    margin-right: 0;
    margin-bottom: 0;
    width: 50%;
  }
</style>



