<template>
  <div class="print">
   <h1>{{ msg }}</h1>
   <h4>{{ content }}</h4>
  </div>
</template>

<script>
export default {
  name: 'print',
  data () {
    return {
      msg: '',
      msg2: 'beautiful',
      current: -10,
      content: '',
      content2: 'girl',
      durrent: -5
    }
  },
  beforeMount () {
    var int = self.setInterval(() => {
      this.printMyLove(this.msg2, this.current, 1)
      if (this.current > this.msg.length) {
        this.printMyLove(this.content2, this.durrent, 2)
        if (this.durrent > this.content.length) {
          clearInterval(int)
        }
        return false
      }
    }, 300)
  },
  mounted () {
  },
  methods: {
    printMyLove (m, i, s) {
      let a = m.substr(0, i)
      switch (s) {
        case 1:
          this.msg = a + '_'
          this.current++
          break
        case 2:
          this.content = a + '_'
          this.durrent++
          break
      }
    }
  }
}
</script>

<style lang="scss" scoped>

</style>



