import axios from 'axios'
axios.defaults.withCredentials = true

let base = 'http://cuicanback.cuican520.com' //  online
// let base = 'http://192.168.1.106:9090' //  guxiaowei
let config = {headers: {'Content-Type': 'application/x-www-form-urlencoded'}}
export const requestLogin = params => { return axios.post(`${base}/background/login/login`, params).then(res => res.data) }
// export const requestLogin = params => { return axios.post(`${base}/background/login/login`, { params: params }) }
//  上传文件
export const getUpload = params => { return axios.get(`${base}/background/pic/upload`, { params: params }) }
//  获取商品列表
export const queryProduct = params => { return axios.get(`${base}/background/prodtype/queryProduct`, { params: params }) }
//  查询品牌
export const querybrand = params => { return axios.get(`${base}/background/productbrand/querybrand`, { params: params }) }
//  查询分类
export const typeList = params => { return axios.get(`${base}/background/prodtype/get_type`, { params: params }) }
//  单品上传
export const shopUpload = params => { return axios.post(`${base}/background/productbrand/upload`, params, config).then(res => res.data) }
// export const shopUpload = params => { return axios.post(`${base}/background/productbrand/upload`, params).then(res => res.data) }
//  获取店铺列表
export const shopType = params => { return axios.get(`${base}/background/shop/shopType`, { params: params }) }
//  商品规则
export const queryrules = params => { return axios.get(`${base}/background/shopingrules/queryrules`, { params: params }) }
//  国家
export const countryall = params => { return axios.get(`${base}/background/country/all`, { params: params }) }
//  商品属性
export const queryproductinfoparameter = params => { return axios.post(`${base}/background/prodtype/queryproductinfoparameter`, params).then(res => res.data) }
//  demo
export const getNewsList = params => { return axios.get(`https://api.it120.cc/meta/cms/news/list`, { params: params }) }
//  获取订单列表
export const orderList = params => { return axios.post(`${base}/background/order/list`, params).then(res => res.data) }
//  订单详情
export const orderDetail = params => { return axios.post(`${base}/background/order/detail`, params).then(res => res.data) }



