<template>
  <div>
    <com-head></com-head>
    <el-row>
      <el-col :span="4">
          <left-nav></left-nav>
      </el-col>
      <el-col :span="20" class="content">
          <transition name="move-fade-right-to-left">
            <router-view class="app-router-view"></router-view>
          </transition>
      </el-col>
    </el-row>
  </div>
</template>
<script>
import comHead from '../../components/comHead.vue'
import leftNav from '../../components/leftNav.vue'
export default {
  name: 'index',
  data () {
    return {
    }
  },
  components: {
    comHead,
    leftNav
  }
}
</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<style scoped>
.grid-content {
    min-height: 36px;
  }
.el-row {
  height: 100%;
}
.el-col-4 {
  height: 100%;
}
.content {
  padding: 30px;
}
</style>



