      //   'salePrice': parseInt(3), // 销售价格
      //   'marketPrice': parseInt(4), //  欧元价格 number -false
      //   'code': parseInt(4), //  产品编号 string -false
      //   'repertory': parseInt(5), //  库存 number
      //   'masterPic': 'https://productpicture.cuican520.com/enteringExamine201803161339957681.jpeg ', //  主图 array
      //   'fromPic': 'https://productpicture.cuican520.com/enteringExamine201803161339957681.jpeg ', //  从图 array
      //   'introduction': '1123', //  商品简介 string
      //   'chooseParams': {'gangbibijiancaizhi': "1"}, //  可选参数 string
      //   'selectParams': {'gangbibijiancaizhi': "1"}, //  筛选参数 string
      //   'otherParams': {'gangbibijiancaizhi': "1"},
      //   'isPostage': false, //  是否包邮 boolean -false
      //   'shopRefundId': 9, //  商品规则id number
      //   'shopId': parseInt(1) //  店铺Id number
      // })
      console.log(params)
      this.$refs[formName].validate((valid) => {
        if (valid) {
          shopUpload(params).then((res) => {
            console.log(res)
          })
        } else {
          return false
        }
      })
    },
    resetForm (formName) {
      this.$refs[formName].resetFields()
    }
  }
}
</script>
<style scoped>
.demo-ruleForm {
  width: 70%;
  margin: 0 auto;
}
</style>



