<template>
 <div>
    <el-autocomplete
      v-model="searchOrderById"
      placeholder="请输入内容"
    ></el-autocomplete>
    <el-table
      :data="tableData"
      @row-dblclick="handleDbclick"
      v-loading="listLoading"
      style="width: 100%">
      <el-table-column
        prop="orderCode"
        label="订单编号">
      </el-table-column>
      <el-table-column
        prop="userName"
        label="昵称">
      </el-table-column>
      <el-table-column
        prop="payCost"
        label="支付金额">
      </el-table-column>
      <el-table-column
        prop="orderDetailCount"
        label="商品数量">
      </el-table-column>
      <el-table-column
        prop="createDate"
        sortable
        width="120"
        label="创建时间">
      </el-table-column>
      <el-table-column
        prop="orderStatus"
        label="订单状态"
        width="120"
        :filters="getOrderStatus"
        :filter-method="filterHandler"
      >
      </el-table-column>
      <el-table-column
        prop="takeType"
        label="提货方式">
      </el-table-column>
      <el-table-column
        prop="totalMoney"
        label="总金额">
      </el-table-column>
       <el-table-column label="操作">
      <template slot-scope="scope">
        <el-button
          size="mini"
          @click="handleEdit(scope.$index, scope.row)">编辑</el-button>
        <el-button
          size="mini"
          type="danger"
          @click="handleDelete(scope.$index, scope.row)">删除</el-button>
      </template>
    </el-table-column>
    </el-table>
    <div style="text-align:center;">
      <el-pagination
      background
      layout="prev, pager, next"
      @current-change="handelCurrentChange"
      :current-page="currentPage"
      :total="total">
    </el-pagination>
  </div>
 </div>
</template>

<script>
import { orderList } from '../../api'
import { mapGetters } from 'vuex'
import qs from 'qs'
export default {
  name: 'com_head',
  data () {
    return {
      tableData: [],
      searchOrderById: '', //  根据订单编号搜索
      currentPage: 1, //  默认第一页
      total: 0, //  总数
      listLoading: false
    }
  },
  mounted () {
    this.getOrderList()
  },
  computed: {
    ...mapGetters(['getOrderStatus'])
  },
  methods: {
    formatter (row, column) {
      return row.address
    },
    filterTag (value, row) {
      return row.tag === value
    },
    filterHandler (value, row, column) { //  按条件筛选
      const property = column['property']
      return row[property] === value
    },
    handleEdit (index, row) { //  编辑
      console.log(index, row)
    },
    handleDelete (index, row) { //  删除
      console.log(index, row)
    },
    handleDbclick (row, event) { //  双击一行
      console.log(row)
      this.$router.push({ path: '/orderDetail', query: row })
    },
    handelCurrentChange (i) {
      console.log(i)
      this.currentPage = i
      this.getOrderList(i)
    },
    getOrderList (i) {
      var para = qs.stringify({
        'pageIndex': i
      })
      this.listLoading = true
      orderList(para).then((res) => {
        console.log(res.data.content)
        this.tableData = res.data.content
        this.total = res.data.count
        this.listLoading = false
      })
    }
  }
}
</script>
<style>
</style>



