<template>
  <div class="orderDetail">
   <el-card class="box-card">
    <div slot="header" class="clearfix">
      <div class="urserImg">
        <div>帅</div>
      </div>
      <div>
        <div class="username">名字: <span>性别</span></div>
        <div class="userInfo">
          <span>生日: 111111</span>
          <span>订单编号: 11111111</span>
          <span>电话: 1111111</span>
          <span>创建时间: 1111111</span>
        </div>
      </div>
    </div>
    <el-table
      ref="multipleTable"
      :data="tableData"
      tooltip-effect="dark"
      style="width: 100%"
      @selection-change="handleSelectionChange">
      <el-table-column
        type="selection"
        width="55">
      </el-table-column>
      <el-table-column
        label="名称"
        width="120">
        <template slot-scope="scope">
          <el-popover trigger="hover" placement="top">
            <p>名称: {{ scope.row.productDescr }}</p>
            <div slot="reference" class="name-wrapper">
              <!-- <el-tag size="medium">{{ scope.row.productDescr.substr(0, 9) }}</el-tag> -->
            </div>
          </el-popover>
        </template>
      </el-table-column>
      <el-table-column
        prop="quantity"
        label="数量">
      </el-table-column>
      <el-table-column
        label="金币">
        <template slot-scope="scope">{{ scope.row.usedGoldb===null?0:scope.row.usedGoldb }}</template>
      </el-table-column>
      <el-table-column
        label="银币">
        <template slot-scope="scope">{{ scope.row.usedSilverb===null?0:scope.row.usedSilverb }}</template>
      </el-table-column>
      <el-table-column
        label="玉币">
        <template slot-scope="scope">{{ scope.row.usedGreenb===null?0:scope.row.usedGreenb }}</template>
      </el-table-column>
      <el-table-column
        label="原价">
        <template slot-scope="scope">{{ scope.row.salePrice===null?0:scope.row.salePrice }}</template>
      </el-table-column>
      <!-- <el-table-column
        label="实付">
        <template slot-scope="scope">{{ scope.row.payCost===null?0:scope.row.payCost }}</template>
      </el-table-column> -->
      <el-table-column label="操作">
        <template slot-scope="scope">
          <el-button
            size="mini"
            @click="handleEdit(scope.$index, scope.row)">编辑</el-button>
          <el-button
            size="mini"
            type="danger"
            @click="handleDelete(scope.$index, scope.row)">删除</el-button>
        </template>
      </el-table-column>
    </el-table>
    <div class="orderInfo">
      <span>共计: {{ this.total }} 件</span>
      <span>实付: {{ this.totalPrice }} 元</span>
    </div>
  </el-card>
  </div>
</template>

<script>
import { orderDetail } from '../../api'
import qs from 'qs'
import _ from 'lodash'
export default {
  name: 'orderDetail',
  data () {
    return {
      orderDetail: {}, //  订单详情
      tableData: [], //  该订单下的所有商品
      multipleSelection: [], //  选中的商品
      total: 0, //  总数量
      totalPrice: 0 //  总价
    }
  },
  mounted () {
    this.getOrderDetail()
  },
  created: function () {
    if (this.$router.app._route.query) {
      const e = this.$router.app._route.query
      const o = this.orderDetail
      o.orderId = e.orderId
    }
  },
  methods: {
    getOrderDetail () {
      var para = qs.stringify({
        'orderId': this.orderDetail.orderId
      })
      orderDetail(para).then((res) => {
        this.tableData = res.data.orderDetailShowList
        // console.log(this.tableData)
      })
    },
    handleSelectionChange (val) { //  选择商品
      var a = []
      var b = []
      this.multipleSelection = val
      for (let i = 0; i < this.multipleSelection.length; i++) {
        a.push(this.multipleSelection[i].quantity) //  计算总数量
        b.push(this.multipleSelection[i].quantity * this.multipleSelection[i].salePrice) //  计算总价
      }
      this.total = _.sum(a)
      this.totalPrice = _.sum(b)
    },
    handleEdit (index, row) { //  编辑
      console.log(index, row)
    },
    handleDelete (index, row) { //  删除
      console.log(index, row)
    }
  }
}
</script>

<style lang="scss" scoped>
.text {
    font-size: 14px;
  }

  .item {
    margin-bottom: 18px;
  }
  .urserImg {
    width: 104px;
    margin-right: 20px;
    text-align: center;
    line-height: 104px;
    div {
      border-radius: 50%;
      background-color: #ddd;
      height: 100%;
    }
  }
  .username {
    line-height: 3em;
  }
  .clearfix {
    display: flex;
    justify-content: flex-start;
  }
  .clearfix:before,
  .clearfix:after {
    display: table;
    content: "";
  }
  .clearfix:after {
    clear: both
  }

  .box-card {
    width: 100%;
  }
  .userInfo {
    span {
      width: 50%;
      display: inline-block;
      float: left;
      line-height: 2em;
      font-size: 14px;
    }
  }
  .orderInfo {
    display: flex;
    justify-content: flex-end;
    span {
      margin: 20px 20px 0 20px;
    }
  }
</style>



